<template>
	<scroll-view class="scrollView mask-scroll-view" scroll-x="true">
		<view v-for="(item, index) in imgList" 
			  :key="index" 
			  style="display: inline-flex;">
			<image class="imgList" 
					:src="item" 
					:data-mask-id="index" 
					@tap="changeMask" />
		</view>
	</scroll-view>
</template>

<script>
	export default{
		props: {
			imgList: {
				type: Array,
				default: []
			},
		},
		methods: { 
			changeMask(e) {
				const { maskId } = e.target.dataset
				this.$emit("click", maskId)
			}
		},
	}
</script>

<style lang="scss" scoped>
	.scrollView {
		width: 100%;
		position: absolute;
		bottom: 30rpx;
		white-space: nowrap;
	}
	.imgList {
		height: 65px;
		width: 65px;
		border: 4rpx solid white;
		border-radius: 5px;
		margin: 10px 10px 10px 10px;
		z-index: 9999999;
	}
</style>
